.calculator {
  width: 500px;
  margin: auto;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
}

* {
  background-color: black;
}
.display {
  font-size: 50px;
  background-color: palegoldenrod;
  border: 1px solid #ccc;
  padding: 10px;
  margin-bottom: 10px;
  text-align: right;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 5px;
}

button {
  font-size: 50px;
  padding: 10px;
  border: none;
  background-color: peru;
  cursor: pointer;
}

button:hover {
  background-color: #aaa;
}

button:active {
  background-color: #888;
}
